<template>
  <aside class="filter mt-5">
    <BLink class="filter-opener d-lg-none" @click.prevent="toggleFilter"
      >Filters<span class="plus">+</span>
    </BLink>
    <div
      class="filter-overlay"
      @click.prevent="toggleFilter"
      :class="{ show: filterExpanded }"
    ></div>
    <div class="fake-overlay" v-if="overlayIsReady" :class="{ show: filterExpanded }"></div>
    <transition name="slide-fade">
      <div class="filter-collapse" :class="{ show: filterExpanded }">
        <ProductFilterContent @clicked="toggleFilter" />
      </div>
    </transition>
  </aside>
</template>

<script setup>
import { onMounted, onUnmounted, ref } from "vue";
import ProductFilterContent from "./ProductFilterContent.vue";

defineProps({
  dontShowActiveFilters: {
    type: Boolean,
  },
});

const filterExpanded = ref(false);
const overlayIsReady = ref(false);

const resize = () => {
  if (window.innerWidth > 991) filterExpanded.value = false;
};

const toggleFilter = () => {
  filterExpanded.value = !filterExpanded.value;
};

onMounted(() => {
  window.addEventListener("resize", resize);
  overlayIsReady.value = true;
});

onUnmounted(() => {
  window.removeEventListener("resize", resize);
});
</script>
