<template>
  <div class="filter-content">
    <div class="d-lg-none filter-top">
      <span class="filter-collapse-heading">{{ $t("filters") }}</span>
      <BLink class="lnr lnr-chevron-left" @click.prevent="toggleFilter"></BLink>
    </div>
    <div class="category" v-if="!dontShowActiveFilters">
      <span class="filter--block-heading">{{ category?.name }}</span>
      <ul class="category-list">
        <li v-for="categoryItem in categories" :key="categoryItem.id">
          <BLink :to="`/` + categoryItem.url_path">{{
            categoryItem.name
          }}</BLink>
        </li>
      </ul>
    </div>
    <div
      class="active-filters"
      v-if="isPriceFilterActive || filterItemsSelected.length"
    >
      <BLink
        class="filter--block-heading filter--collapse"
        v-b-toggle.active-filters
        @click.prevent
        >{{ $t("used_filters") }}:</BLink
      >
      <BLink
        href="#"
        class="delete-active-filters"
        @click.prevent="removeAllActiveFilter(filterItemsSelected)"
        ><i class="lnr lnr-cross"></i
      ></BLink>
      <BCollapse visible id="active-filters">
        <ul class="active-filters-list">
          <li v-if="isPriceFilterActive">
            <BLink @click.prevent="removePriceFilter">
              <i class="lnr lnr-cross"></i>
            </BLink>
            <span class="active-filter--name">
              {{ `${priceFilterLabel ? priceFilterLabel : "Prijs"}` }}:
              {{ priceFilter[0] + " - " + priceFilter[1] }}
            </span>
          </li>
          <li
            v-for="(activeFilterItem, index) of filterItemsSelected"
            :key="index"
          >
            <BLink
              @click.prevent="removeSelectedItem(activeFilterItem.value)"
              href="#"
              ><i class="lnr lnr-cross"></i
            ></BLink>
            <span class="active-filter--name"
              >{{ activeFilterItem?.type_label }}:
              {{ activeFilterItem?.label }}</span
            >
          </li>
        </ul>
      </BCollapse>
    </div>
    <div v-for="prodFilter of productFilters" :key="prodFilter.attribute_code">
      <div class="price" v-if="prodFilter.attribute_code == 'price'">
        <BLink
          class="filter--block-heading filter--collapse"
          v-b-toggle.price-filter
          @click.prevent
          >{{ $t(prodFilter.label) }}</BLink
        >
        <BCollapse visible id="price-filter">
          <div class="range-slider">
            <div>
              <VueRangeSlider
                :min="priceFilterMin"
                :max="priceFilterMax"
                :enable-cross="enableCross"
                :formatter="formatter"
                v-model="priceFilter"
                :logarithmic="true"
                tooltip=""
                :lazy="true"
                @dragging="
                  (value, index) => {
                    if (index) {
                      max = value[index];
                    } else {
                      min = value[index];
                    }
                  }
                "
              >
              </VueRangeSlider>
            </div>

            <div class="range-values d-flex justify-content-between">
              <span>€{{ min ?? priceFilter[0] }}</span>
              <span>€{{ max ?? priceFilter[1] }}</span>
            </div>
          </div>
        </BCollapse>
      </div>

      <div
        class="brands"
        v-if="
          prodFilter.attribute_code != 'size' &&
          prodFilter.attribute_code != 'price'
        "
      >
        <BLink
          class="filter--block-heading filter--collapse"
          v-b-toggle="`brands-filter-${prodFilter.attribute_code}`"
          @click.prevent
          >{{ $t(prodFilter.label) }}</BLink
        >
        <BCollapse
          visible
          :id="`brands-filter-${prodFilter.attribute_code}`"
          class="brands-filter"
        >
          <div
            v-for="(brandsFilterItem, index) in prodFilter.options"
            :key="brandsFilterItem.value"
          >
            <div v-if="index < 10">
              <div class="d-flex justify-content-between">
                <label
                  :for="brandsFilterItem.value"
                  class="d-flex align-items-center"
                >
                  <input
                    type="checkbox"
                    :key="brandsFilterItem.value"
                    :name="brandsFilterItem.label"
                    :id="brandsFilterItem.value"
                    :ref="
                      (el) => {
                        refs[brandsFilterItem.value] = el;
                      }
                    "
                    :value="
                      `attribute_code:` +
                      prodFilter.attribute_code +
                      `,type_label:` +
                      prodFilter.label +
                      `,value:` +
                      brandsFilterItem.value +
                      `,label:` +
                      brandsFilterItem.label
                    "
                    @click="onChangeFilter($event)"
                  />
                  <span class="fake-input"></span>
                  <span class="fake-label">{{ brandsFilterItem?.label }}</span>
                </label>
                <span class="number-available"
                  >({{ brandsFilterItem?.count }})</span
                >
              </div>
            </div>
            <div v-else>
              <div
                v-if="readMore[prodFilter.attribute_code]"
                class="d-flex justify-content-between"
              >
                <label
                  :for="brandsFilterItem.value"
                  class="d-flex align-items-center"
                >
                  <input
                    type="checkbox"
                    :key="brandsFilterItem.value"
                    :name="brandsFilterItem.label"
                    :id="brandsFilterItem.value"
                    :ref="
                      (el) => {
                        refs[brandsFilterItem.value] = el;
                      }
                    "
                    :value="
                      `attribute_code:` +
                      prodFilter.attribute_code +
                      `,type_label:` +
                      prodFilter.label +
                      `,value:` +
                      brandsFilterItem.value +
                      `,label:` +
                      brandsFilterItem.label
                    "
                    @click="onChangeFilter($event)"
                  />
                  <span class="fake-input"></span>
                  <span class="fake-label">{{ brandsFilterItem?.label }}</span>
                </label>
                <span class="number-available"
                  >({{ brandsFilterItem?.count }})</span
                >
              </div>
            </div>
          </div>
          <div
            style="width: 100%; text-align: left"
            v-if="prodFilter.options.length > 10"
          >
            <div
              class="show-more-filter"
              v-if="!readMore[prodFilter.attribute_code]"
            >
              <BLink
                class="filter--block-heading"
                style="display: block; cursor: pointer"
                @click.prevent="showMore(prodFilter.attribute_code)"
                >Toon meer</BLink
              >
            </div>
            <div
              class="show-more-filter"
              v-if="readMore[prodFilter.attribute_code]"
            >
              <BLink
                class="filter--block-heading"
                style="display: block; cursor: pointer"
                @click.prevent="showLess(prodFilter.attribute_code)"
              >
                Toon minder
              </BLink>
            </div>
          </div>
        </BCollapse>
      </div>
      <div class="size" v-if="prodFilter.attribute_code == 'size'">
        <BLink
          class="filter--block-heading filter--collapse mb-15"
          v-b-toggle.size-filter
          @click.prevent
          >Kies je maat</BLink
        >
        <BCollapse visible id="size-filter" class="size-filter">
          <ul class="available-sizes">
            <li
              v-for="sizeFilterItem in prodFilter.options"
              :key="sizeFilterItem.value"
            >
              <label
                :for="sizeFilterItem.name"
                class="d-flex align-items-center"
              >
                <input
                  type="checkbox"
                  :key="sizeFilterItem.value"
                  :id="sizeFilterItem.value"
                  :name="sizeFilterItem.label"
                  :ref="
                    (el) => {
                      refs[sizeFilterItem.value] = el;
                    }
                  "
                  :value="
                    `attribute_code:` +
                    prodFilter.attribute_code +
                    `,type_label:` +
                    prodFilter.label +
                    `,value:` +
                    sizeFilterItem.value +
                    `,label:` +
                    sizeFilterItem.label
                  "
                  @change="onChangeFilter($event)"
                />
                <span class="fake-input"></span>
                <span class="fake-label">{{ sizeFilterItem?.name }}</span>
              </label>
            </li>
          </ul>
          <div
            style="width: 100%; text-align: left"
            v-if="prodFilter.options.length > 5"
          >
            <div
              class="show-more-filter"
              v-if="!readMore[prodFilter.attribute_code]"
            >
              <BLink
                class="filter--block-heading"
                style="display: block; cursor: pointer"
                @click.prevent="showMore(prodFilter.attribute_code)"
                >Toon meer</BLink
              >
            </div>
            <div
              class="show-more-filter"
              v-if="readMore[prodFilter.attribute_code]"
            >
              <BLink
                class="filter--block-heading"
                style="display: block; cursor: pointer"
                @click.prevent="showLess(prodFilter.attribute_code)"
              >
                Toon minder
              </BLink>
            </div>
          </div>
        </BCollapse>
      </div>
    </div>
  </div>
</template>

<script setup>
import { ref, computed, onMounted, onUpdated } from "vue";
import { useStore } from "vuex";
import { useRouter, useRoute } from "vue-router";
import VueRangeSlider from "vue-3-slider-component";
import { reactive } from "vue";

defineProps({
  dontShowActiveFilters: Boolean,
});

const store = useStore();
const router = useRouter();
const route = useRoute();

// const value = ref([0, 200]);
const readMore = ref({});
const min = ref(null);
const max = ref(null);
const enableCross = ref(false);
const formatter = ref("€ {value}");
// const tooltipDir = ref("bottom");

// Refs for DOM elements
const refs = reactive({});

const emit = defineEmits(["clicked"]);

const filterItemsSelected = computed(
  () => store.getters["category/getActiveFilter"]
);

const category = computed(() => {
  const catData = store.getters["category/getCurrentCategory"];
  if (catData) {
    catData.name = catData.name.charAt(0).toUpperCase() + catData.name.slice(1);
    if (catData.children) {
      catData.children.forEach((subcat, index) => {
        var newName =
          subcat.name.charAt(0).toUpperCase() + subcat.name.slice(1);
        catData.children[index].name = newName;
      });
    }
  }
  return catData;
});

const isPriceFilterActive = computed(
  () => store.getters["category/getIsPriceFilterActive"]
);
const priceFilterMin = computed(
  () => store.getters["category/getPriceFilterMin"]
);
const priceFilterMax = computed(
  () => store.getters["category/getPriceFilterMax"]
);

const priceFilterLabel = computed(() => {
  if (productFilters.value) {
    const priceFilter = productFilters.value.find(
      (filter) => filter.attribute_code == "price"
    );
    return priceFilter && priceFilter.label;
  }
  return null;
});

const priceFilter = computed({
  get() {
    return store.getters["category/getPriceFilter"];
  },
  set(val) {
    store.commit("category/setPriceFilter", { data: val, store });
    const path = "/" + category.value.url_path;
    const query = { ...route.query };
    if (isPriceFilterActive.value) {
      query["filter-price"] = `${val[0]}-${val[1]}`;
    } else {
      delete query["filter-price"];
    }
    delete query["page"];
    router.replace({ path, query });
    min.value = null;
    max.value = null;
  },
});

const productFilters = computed(() => {
  const prodFilters = store.getters["category/getProductFilters"];
  if (prodFilters != null && Symbol.iterator in Object(prodFilters)) {
    prodFilters.forEach((filter, index) => {
      prodFilters[index].label =
        filter.label.charAt(0).toUpperCase() + filter.label.slice(1);
      prodFilters[index].options.forEach((option, optIndex) => {
        prodFilters[index].options[optIndex].label =
          option.label.charAt(0).toUpperCase() + option.label.slice(1);
      });
    });
  }
  return prodFilters;
});

const categories = computed(() => category.value?.children);

const toggleFilter = (event) => {
  emit("clicked", event);
};

const showMore = (id) => {
  readMore.value[id] = true;
};

const showLess = (id) => {
  readMore.value[id] = false;
};

const onChangeFilter = (event) => {
  const evSplit = event.target.value.split(",");
  const filter = {
    attribute_code: "",
    type_label: "",
    value: "",
    label: "",
  };
  evSplit.forEach((element) => {
    const part = element.split(":");
    if (part[0] == "attribute_code") {
      filter.attribute_code = part[1];
    } else if (part[0] == "type_label") {
      filter.type_label = part[1];
    } else if (part[0] == "value") {
      filter.value = part[1];
    } else if (part[0] == "label") {
      filter.label = part[1];
    }
  });

  if (typeof filter.value != "undefined") {
    if (event.target.checked) {
      store.commit("category/setActiveFilter", { data: filter, store });
    } else {
      store.commit("category/removeActiveFilter", { data: filter, store });
    }
  }
  updateUrlFilters();
};

const removeSelectedItem = (id) => {
  const filter = { value: id };
  if (refs && refs[id]) {
    refs[id].checked = false;
  }
  store.commit("category/removeActiveFilter", { data: filter, store });
  updateUrlFilters();
};

const removePriceFilter = () => {
  const priceFilter = [priceFilterMin.value, priceFilterMax.value];
  store.commit("category/setPriceFilter", { data: priceFilter, store });
  const path = "/" + category.value.url_path;
  const query = { ...route.query };
  delete query["filter-price"];
  delete query["page"];
  router.replace({ path, query });
};

const removeAllActiveFilter = (filterItemsSelected) => {
  filterItemsSelected.forEach((filterItem) => {
    const { value } = filterItem;
    // Check if the ref exists and uncheck it
    if (refs && refs[value]) {
      refs[value].checked = false;
    }
  });
  store.commit("category/removeAllActiveFilter", store);
  updateUrlFilters(true);
};

const updateUrlFilters = (removeAllFilter = false) => {
  const activeFilters = {};
  filterItemsSelected.value.forEach((val) => {
    if (activeFilters[val.attribute_code]) {
      activeFilters[val.attribute_code].push(val.value);
    } else {
      activeFilters[val.attribute_code] = [val.value];
    }
  });

  const path = "/" + category.value.url_path;
  const query = { ...route.query };

  for (const q in query) {
    if (q.startsWith("filter-") && removeAllFilter) {
      delete query[q];
    } else if (q.startsWith("filter-") && q !== "filter-price") {
      delete query[q];
    }
  }

  for (const attr in activeFilters) {
    query[`filter-${attr}`] = activeFilters[attr].toString();
  }
  delete query["page"];

  router.replace({ path, query });
};

// Lifecycle Hooks
onMounted(() => {
  filterItemsSelected.value.forEach((filterItem) => {
    refs[filterItem.value].checked = true;
  });
});

onUpdated(() => {
  if (filterItemsSelected.value && filterItemsSelected.value.length) {
    filterItemsSelected.value.forEach((filter) => {
      if (refs && refs[filter.value]) {
        refs[filter.value].checked = true;
      }
    });
  }
});
</script>
